

.row {

    justify-content: center ;
}



.search {

    font-size: 2rem;
    transition: 0.5s ease;
    padding: 2rem 0.5rem;
}

.search:focus {

    box-shadow: 2px 2px 15px #7700ff;
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
}

.btn {

    font-size: 2rem;
    padding: 0.5rem 3rem;
}