@import url('https://fonts.googleapis.com/css2?family=Audiowide&family=Rowdies:wght@300&display=swap');


/* Fonts *************** */

.audio-wide {

  font-family: 'Audiowide', cursive ;
}

.row-die {

  font-family: 'Rowdies', cursive

}


html {

  font-size: 62.5%;
}

body{

  font-size: 1.8rem;
  background-image: url(../img/banner.jpg);
  background-size: cover;
  background-attachment: fixed;

  height: 100%;
}



.main {

 
  position: relative;
  padding: 0 0.5rem;



}




.jumbotron { 

  width: 90%;
  background-color: #00000070;
  border: 1px solid #fff;
  margin: 5rem  auto;


}

.context  h2{
  font-size: 5rem;
  text-shadow: 2px 2px 5px #000;
  text-align: center;
}

.context  h2 span {

  font-size: 5.5rem;
  color: orange;
}

.context  p {

  font-size: 2rem;
  text-shadow: 2px 2px 5px #000;
  color: #00dd00;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  text-shadow: 2px 2px 5px  blue;
  text-align: center;

}


.line {

  width: 55%;
  margin: 0 auto;
  border-bottom: 2px solid orange;
}




